import React from 'react';
import ReactContentTabs from 'react-content-tabs';
import styled from 'styled-components';

import HeroSection from '../../components/HeroSection/HeroSection';
import CodeBlock from '../../components/CodeBlock/CodeBlock';

const Title = styled.h1`
  color: #fff;
  font-family: 'Impact', sans-serif;
  font-size: 4rem;
  line-height: 2;
  text-transform: uppercase;
`;

const GitHubWrapper = styled.a.attrs({
  href: 'https://github.com/yuschick/react-content-tabs',
})`
  align-items: center;
  color: #fff;
  display: flex;
  font-family: 'Impact', sans-serif;
  letter-spacing: 1px;
  position: absolute;
  right: 0;
  text-decoration: none;
  text-transform: uppercase;
  top: 0;

  img {
    margin-left: 1rem;
    width: 50px;
  }
`;

const App = () => (
  <HeroSection color="#e92525">
    <div>
      <GitHubWrapper>
          <span>View Documentation on </span>
          <img src="https://image.flaticon.com/icons/svg/25/25231.svg" alt="Visit on GitHub" />
      </GitHubWrapper>
      <Title>React Content Tabs</Title>
      <ReactContentTabs
        tabPlacement="center"
        tabs={[
          {
            id: 'yarn',
            title: 'Yarn',
            content: () => <CodeBlock>yarn add react-content-tabs</CodeBlock>,
          },
          {
            id: 'npm',
            title: 'NPM',
            content: () => <CodeBlock>npm install react-content-tabs -D</CodeBlock>,
          },
        ]}
        tabStyles={{
          fontSize: '2rem',
          fontWeight: 700,
          textTransform: 'uppercase'
        }}
        theme={{
          base: '#e92525',
          primary: "#fff",
          tertiary: "#111"
        }}
      />
    </div>
  </HeroSection>
);

export default App;
